
//humbergar button
#navToggle{
  display: none;
  @include max-screen($BREAK-POINT-SM) {
    display: block;
    position: fixed;//固定にしない場合
    left:16px;
    top:16px;
    width:56px;
    height:52px;
    cursor:pointer;
    z-index: 9999;
    background-color: $VIVID-BG-COLOR;
    padding: 15px 12px;
    div {
      position:relative;
      span {
        display:block;
        position:absolute;
        width:100%;
        border-bottom:solid 1px #fff;
        transition: .35s ease-in-out;
      }
      span:nth-child(1) {top:0;}
      span:nth-child(2) {top:10px;}
      span:nth-child(3) {
        top:20px;
      }
    }
  }
}
.openNav {
  #navToggle {
    span:nth-child(1) {
      top: 11px;
      transform:rotate(-45deg);
    }
    span:nth-child(2),
    span:nth-child(3) {
      top: 11px;
      transform:rotate(45deg);
    }
  }
  nav {
    transform: translateX(100%)!important;
  }
}

//nav
nav {
  background-color: $VIVID-BG-COLOR;
  border-top: 1px solid #707070;
  border-bottom: 1px solid #707070;
  transition: 0.5s;
  &.nav-top {
    position: absolute;
    left: 0;
    bottom: 0;
  }
  @include max-screen($BREAK-POINT-SM) {
    &.nav-top {
      position: fixed;
      left: -100%;
      top: 0;
    }
    position: fixed;
    left: -100%;
    top: 0;
    transition: all 0.6s ease;
    height: 100vh;
    padding-top: 82px;
    z-index: 8888;
  }
}
nav .section-inner {
  align-items: center;
}
.nav-item {
  flex: 1 0 auto;//余白を等幅で表示
  padding: 15px;
  @include max-screen($BREAK-POINT-SM) {
    position: relative;
    &:after {
      content: " ";
      display: block;
      margin: 0 auto;
      width: 20px;
      height: 1px;
      background-color: lighten($VIVID-BG-COLOR, 16%);
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }
    &:last-child:after {
      width: 0%;
    }
  }
  //現在ページではリンク無効化
  &.current {
    a {
      pointer-events: none;
    }
  }
  a {
    display: block;
    padding: 15px 0;
    text-align: center;
    vertical-align: middle;
    transition: 0.3s;
    margin: 0 auto;
    position: relative;
    @include max-screen($BREAK-POINT-TB) {
      padding: 10px 0px;
    }
    // nav hover animation
    &:after, &:before {
      content: " ";
      display: block;
      margin: 0 auto;
      width: 100%;
      height: 1.2px;
      background-color: lighten($VIVID-BG-COLOR, 16%);
      position: absolute;
      transform: scale(0, 1);
      transform-origin: center top;
      transition: 0.5s;
    }
    &:before {
      top: 0;
      left: 0;
    }
    &:after {
      bottom: 0;
      left: 0;
    }
    &:hover:after,
    &:hover:before {
      transform: scale(1, 1);
    }
    @include max-screen($BREAK-POINT-SM) {
      &:hover:after,
      &:hover:before {
        transform: scale(0, 1);
      }
    }
  }
}
